	module.exports = "<!--\n  Other classes:\n\n  rs-state-initial\n  rs-state-choose\n  rs-state-sign-in\n  rs-state-connected\n\n  rs-floating        - will make it fixed in the right corner\n-->\n<div class=\"rs-widget rs-state-initial\">\n\n  <svg class=\"rs-main-logo\" xmlns=\"http://www.w3.org/2000/svg\" version=\"1.1\"\n       xml:space=\"preserve\" width=\"0.739008in\" height=\"0.853339in\"\n       style=\"shape-rendering:geometricPrecision; text-rendering:geometricPrecision; image-rendering:optimizeQuality; fill-rule:evenodd; clip-rule:evenodd\"\n       viewBox=\"0 0 739 853\"\n       xmlns:xlink=\"http://www.w3.org/1999/xlink\">\n    <g id=\"rs-logo-shape\">\n     <polygon class=\"rs-logo-shape\" points=\"370,754 0,542 0,640 185,747 370,853 554,747 739,640 739,525 739,525 739,476 739,427 739,378 653,427 370,589 86,427 86,427 86,361 185,418 370,524 554,418 653,361 739,311 739,213 739,213 554,107 370,0 185,107 58,180 144,230 228,181 370,100 511,181 652,263 370,425 87,263 87,263 0,213 0,213 0,311 0,378 0,427 0,476 86,525 185,582 370,689 554,582 653,525 653,590 653,592 \"/>\n    </g>\n  </svg>\n\n  <!-- <img role=\"button\" class=\"rs-close\" width=\"40\" src=\"\"> -->\n\n  <div class=\"rs-box-initial rs-clearfix\">\n    <h3 class=\"rs-small-headline\">Connect Remote Storage</h3>\n    <span class=\"rs-sub-headline\">To own and control your data</span>\n  </div>\n\n  <div class=\"rs-box-connected rs-clearfix\">\n    <div class=\"rs-connected-text\">\n      <h1 class=\"rs-user rs-small-headline\">user@provider.com</h1>\n      <span class=\"rs-sub-headline\">Online</span>\n    </div>\n    <div class=\"rs-connected-buttons\">\n      <button class=\"rs-button rs-button-small rs-disconnect\" title=\"Disconnect\">\n        <img class=\"rs-icon rs-power-icon\" height=\"16\" width=\"16\" src=\"\">\n      </button>\n      <button class=\"rs-button rs-button-small rs-sync\" title=\"Sync now\">\n        <img class=\"rs-icon rs-loop-icon\" height=\"16\" width=\"16\" src=\"\">\n      </button>\n    </div>\n  </div>\n\n  <div class=\"rs-box-error hidden\"></div>\n\n  <div class=\"rs-box-choose\">\n    <div class=\"rs-box-choose-content\">\n      <h3 class=\"rs-big-headline\">Own Your Data</h3>\n      <p class=\"rs-short-desc\">\n        This app allows you to own and control your data.\n        <a class=\"rs-help\" href=\"https://remotestorage.io/\" target=\"_blank\">Read more</a>\n      </p>\n      <p class=\"rs-choose-backend\">\n        Please choose your storage:\n      </p>\n      <div class=\"rs-button-wrap\">\n        <button class=\"rs-button rs-button-big rs-choose-rs\">\n          <img class=\"rs-logo\" width=\"40\" height=\"40\" src=\"\">\n          <div>RemoteStorage</div>\n        </button>\n        <button class=\"rs-button rs-button-big rs-choose-dropbox\">\n          <img class=\"dropbox-logo\" width=\"40\" height=\"40\" src=\"\">\n          <div>Dropbox</div>\n        </button>\n        <button class=\"rs-button rs-button-big rs-choose-gdrive\">\n          <img class=\"gdrive-logo\" width=\"40\" height=\"40\" src=\"\">\n          <div>Google Drive</div>\n        </button>\n      </div>\n    </div>\n  </div>\n\n  <div class=\"rs-box-sign-in\">\n    <div class=\"rs-sign-in-content\">\n      <form name=\"rs-sign-in-form\" class=\"rs-sign-in-form\">\n        <h1 class=\"rs-big-headline\">Connect your storage</h1>\n        <input type=\"text\" name='rs-user-address' placeholder=\"user@provider.com\">\n        <div class=\"rs-sign-in-error hidden\"></div>\n        <input type=\"submit\" value=\"Connect\">\n        <a href=\"https://remotestorage.io/get/\" class=\"rs-help\" target=\"_blank\">Need help?</a>\n      </form>\n    </div>\n  </div>\n\n</div>\n"

